import React, { Component } from 'react';
import '../assets/Style.scss';
import Scrollspy from 'react-scrollspy';  //IMPORTED SCROLLSPY
//FONT AWESOME IMPORTS
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faReact } from '@fortawesome/free-brands-svg-icons';
import { faBootstrap } from '@fortawesome/free-brands-svg-icons';
//FONT AWESOME IMPORTS

class SideNav extends Component {
    state = {  }
    render() { 
        return ( 
            <nav id="sidenav" className = "nav-column">
                <Scrollspy items={ ['home', 'about-me', 'projects', 'contact'] } currentClassName="active-link" componentTag={ "a" } >
                    <li><a class="nav-link" href="#home">Home</a></li>
                    <li><a class="nav-link" href="#about-me">About Me</a></li>
                    <li><a class="nav-link" href="#projects">Projects</a></li>
                    <li><a class="nav-link" href="#contact">Contact</a></li>
                </Scrollspy>
                <div className = "site-info">
                    <h3>
                        Made by me w/: 
                        &nbsp;<span><a href = "https://reactjs.org/" target = "_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faReact} /></a></span>&nbsp; 
                        x 
                        &nbsp;<span><a href = "https://getbootstrap.com/" target = "_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faBootstrap} /></a></span>
                    </h3>
                </div>
            </nav>
         );
    }
}
 
export default SideNav;