import React, { Component } from 'react';
import '../assets/Style.scss';
//FONT AWESOME IMPORTS
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLinkedin } from '@fortawesome/free-brands-svg-icons';
import { faTwitter } from '@fortawesome/free-brands-svg-icons';
import { faMailBulk } from '@fortawesome/free-solid-svg-icons';
//FONT AWESOME IMPORTS

import { Parallax } from 'react-scroll-parallax';

class Contact extends Component {
    state = {  }
    render() { 
        return (
            <div className = "contact-container" id = "contact">
                <Parallax x={[-4, 4]}>
                <h1 >How to reach me</h1>
                <br/>
                </Parallax>

                <Parallax x={[8, -10]}>
                <div className = "mail shadow">
                    <FontAwesomeIcon icon={faMailBulk} />
                    <h3><a href="mailto:nick.nikolos@outlook.com">nick.nikolos@outlook.com</a></h3>
                </div>
                </Parallax>
                <Parallax x={[-6, 10]}>
                    <div className = "social shadow">
                        <div className = "linkedin">
                            <a href="https://www.linkedin.com/in/nick-nikolos-a208691aa/" target = "_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faLinkedin} /></a>
                        </div>
                        <h4>or</h4>
                        <div className = "twitter">
                            <a href="https://twitter.com/nick_nkls" target = "_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faTwitter} /></a>
                        </div>
                    </div>
                </Parallax>
            </div>
         );
    }
}
 
export default Contact;