import React, { Component } from 'react';
import '../assets/Style.scss';
import heroImg from '../assets/css_modules/images/hero-image.png'

import { Parallax } from 'react-scroll-parallax';

class Home extends Component {
    state = {  }
    render() { 
        return ( 
            <div className = "home-container" id = "home">
                <div className = "hero-container">
                    <div className = "hero-text">
                        <h1>
                            Hi!<br/>
                            My name is<br/><span className = "name">Nick<br/>Nikolos</span><br/>
                            <span className = "proffesion">&lt; Developer &gt;</span>
                        </h1>
                    </div>
                    
                    <div className = "hero-image" >
                        <Parallax x={[9, -9]}>
                            <img src = {heroImg} draggable={false} className = "img-fluid shadow" alt = "Hero Img"/> 
                        </Parallax>    
                    </div>
                </div>
            </div>
         );
    }
}
 
export default Home;