import React, { Component } from 'react';
import '../assets/Style.scss';
//FONT AWESOME IMPORTS
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCuttlefish, faReact, faPython } from '@fortawesome/free-brands-svg-icons';
import { faHashtag } from '@fortawesome/free-solid-svg-icons';
//FONT AWESOME IMPORTS

import { Parallax } from 'react-scroll-parallax';


class AboutMe extends Component {
    state = {  }
    render() { 
        return ( 
            <div className = "about-me-container" id = "about-me">
                <h1 >About Me</h1>
                <div className = "few-words">
                    <br/>
                    <p>
                        Graduated from the University of Peloponnese in 2019 with a <span>BSc</span> in <span>Computer Science</span>.<br/>
                        I've tackled on many projects, from simple
                        <span> websites</span> and desktop applications,<br/>
                        to native and cross platform <span>mobile apps</span>.<br/><br/>
                        I like to develop and design all kinds of stuff.<br/>
                        Adobe Illustrator is my friend! <br/><br/>
                        Also, I love movies and <span>videogames</span>. <br/>
                    </p>
                    <br/>
                </div>

                <div className = "favorite-languages">
                    <h1>What I like using</h1>
                    <h4><FontAwesomeIcon icon={faCuttlefish} /><FontAwesomeIcon icon={faHashtag} /></h4>
                    <h4><FontAwesomeIcon icon={faPython} /></h4>
                    <h4><FontAwesomeIcon icon={faReact} /></h4>

                    <Parallax x={[-3, 3]}>
                        <div className = "favorite-tools shadow">
                            <h1>My Productivity Tools</h1>
                            <img src="https://img.icons8.com/fluent/48/000000/spotify.png" alt = "Spotify"/>
                            <h4 style = {{display:"inline-block"}}>&nbsp;+&nbsp;</h4>
                            <img src="https://img.icons8.com/color/48/000000/visual-studio-code-2019.png" alt = "VS Code"/>
                            <h4 style = {{display:"inline-block"}}>&nbsp;+&nbsp;</h4>
                            <img src="https://img.icons8.com/fluent/48/000000/coffee-to-go.png" alt = "Coffee"/>
                        </div>
                    </Parallax>
                </div>
            </div>
         );
    }
}
 
export default AboutMe;