import React, { Component } from 'react';
import '../assets/Style.scss';
import quake from '../assets/css_modules/images/quakeIcon.png';
import tnMoto from '../assets/css_modules/images/tnMoto.jpeg';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGooglePlay } from '@fortawesome/free-brands-svg-icons';

class Projects extends Component {
    state = {  }
    render() { 
        return ( 
            <div className = "projects-container" id = "projects">
                <h1>Things I've worked on</h1>
                <br/>
                <div className = "project-cards">
                    <div class="card shadow" style= {{width: 20+'rem'}}>
                        <img class="card-img-top" src={quake} alt="Quake cap"/>
                        <div class="card-body">
                            <h5 class="card-title">Quake</h5>
                            <p class="card-text">Quake is an Android app, that displays earthquake occurrences around the world.<br/><br/>
                                <h4>Language used</h4>
                                Java<br/><br/> 
                                <h4>Developed with</h4>
                                Android Studio<br/><br/> 
                                <h4>Designed in</h4>
                                Adobe Illustrator
                            </p>
                            <a href="https://play.google.com/store/apps/details?id=com.natdis.quake" target="_blank" rel="noopener noreferrer" class="btn btn-primary">Get <FontAwesomeIcon icon={faGooglePlay} /></a>
                        </div>
                    </div>
                    <div class="card shadow" style= {{width: 30+'rem'}}>
                        <img class="card-img-top" src={tnMoto} alt="TN cap"/>
                        <div class="card-body">
                            <h5 class="card-title">TN Moto</h5>
                            <p class="card-text">A website for the TN Moto company, featuring responsive design and CMS.<br/><br/>
                            <h4>Language used</h4>
                                HTML, CSS, JavaScript<br/><br/> 
                                <h4>Developed with</h4>
                                VS Code, Git Bash w/ Node.js<br/><br/> 
                                <h4>Designed in</h4>
                                Adobe XD &amp; Illustrator
                            </p>
                            {/* <a href="#" class="btn btn-primary">Go somewhere</a> */}
                        </div>
                    </div>
                </div>
            </div>
         );
    }
}
 
export default Projects;